* {
  margin: 0;
  padding: 0;
}

div.full {
  width: 100%;
  height: 100%;
}

svg {
  width: 100%;
  height: 100%;
}

text {
  -webkit-touch-callout: none;
  -webkit-user-select:none;
  -khtml-user-select:none;
  -moz-user-select:none;
  -ms-user-select:none;
  -o-user-select:none;
  user-select:none;
}